.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header-navbar {
  margin-right: 20px;
  display: flex;
}
.header-navbar .navlink {
  background: grey;
  color: #ffe81f;
  padding: 10px 20px;
  border: 1px solid #ffffff;
  border-radius: 30px;
  margin-left: 10px;
  min-width: 150px;
  text-align: center;
  &:hover {
    background: #000000;
    cursor: pointer;
  }
}
